/*排行榜详情*/

import axios from 'axios'
import jsonp from 'common/js/jsonp.js'
import {
  commonParams
} from './config'

export function getTopDetail(topId) {
  let url = 'https://u.y.qq.com/cgi-bin/musicu.fcg?'
  let pdata = {
    '-': 'getUCGI2243510197798395',
    needNewCode: 0,
    data: {
      "detail": {
        "module": "musicToplist.ToplistInfoServer",
        "method": "GetDetail",
        "param": {
          "topId": topId,
          "offset": 0,
          "num": 20,
          "period": "2019_12"
        }
      },
      "comm": {
        "ct": 24,
        "cv": 0
      }
    }
  }

  let data = JSON.stringify(pdata.data)
  pdata = Object.assign(pdata, commonParams, {
    loginUin: 0,
    hostUin: 0,
    platform: 'yqq.json',
    data: data
  })

  if (process.env.NODE_ENV === 'production') {
    return jsonp(url, pdata, {});
  } else {
    url = '/api/getTopDetail'
    return axios.get(url, {
      params: pdata
    }).then((res) => {
      return Promise.resolve(res.data)
    }).catch((err) => {
      console.log(err);
    })

  }

}



